/* animation */

@-webkit-keyframes moveBg{
  0%{
    background-position:0% 0%;
  }
  100%{
    background-position:100% 0%;
  }
}

@keyframes moveBg{
  0%{
    background-position:0% 0%;
  }
  100%{
    background-position:100% 0%;
  }
}


  
